import conf from '../conf'

const SIMPLER = conf.SIMPLER
const LEADER = conf.LEADER

const EMPTY = { name: '普通會員', type: SIMPLER, star: 0 }

const VIPS_SIMPLER = [
    { name: '一級會員', type: SIMPLER, star: 1, img: 'ICONS/vip_simpler_1.png' },
    { name: '二級會員', type: SIMPLER, star: 2, img: 'ICONS/vip_simpler_2.png'  },
    { name: '三級會員', type: SIMPLER, star: 3, img: 'ICONS/vip_simpler_3.png'  },
    { name: '四級會員', type: SIMPLER, star: 4, img: 'ICONS/vip_simpler_4.png'  },
    { name: '五級會員', type: SIMPLER, star: 5, img: 'ICONS/vip_simpler_5.png'  },
]
const VIPS_LEADER = [
    { name: '一星領袖', type: LEADER, star: 1, img: 'ICONS/vip_leader_1.png'  },
    { name: '二星領袖', type: LEADER, star: 2, img: 'ICONS/vip_leader_2.png'  },
    { name: '三星領袖', type: LEADER, star: 3, img: 'ICONS/vip_leader_3.png'  },
    { name: '四星領袖', type: LEADER, star: 4, img: 'ICONS/vip_leader_4.png'  },
    { name: '五星領袖', type: LEADER, star: 5, img: 'ICONS/vip_leader_5.png'  },
]

//
const code_to_content = function(rec) {
    let res = ''

    if (rec == undefined) { return EMPTY }

    if (rec[0] == SIMPLER) {

        if (rec[1] == 0) { return res }
        res = VIPS_SIMPLER[ rec[1] - 1]
    } else {

        res = VIPS_LEADER[ rec[1] - 1]
    }
    return res
}

const split_code = function(rec, option = true) {

    let res = null

    if (option) {

        res = rec.split('_')
    } else {

        res = rec.join('_')
    }

    return res
}

export default {
    split_code,
    code_to_content
}


